@import url(https://fonts.googleapis.com/css?family=Quicksand|Montserrat|Oxygen);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.not-selected{
    padding-bottom: 5px;
    cursor: pointer;
    width: 100%;
    margin-top: 5px;
    padding-top: 8px;
    background: #e0dfdf1f;
    border-radius: 5px;
    border-color: #eaeaea;
    border-style: solid;
    border-width: 2px;
    transition: 0.3s;
    opacity: 1 !important;
}

.selected{
    margin-top: 10px;
    margin-bottom: 10px;
    padding-top: 8px; 
    transition: 0.3s; 
    border-radius: 0; 
    border-color: transparent; 
    border-style: solid; 
    border-width: 2px; 
    position: -webkit-sticky; 
    position: sticky; 
    top: 0; 
    bottom: 0; 
    background: white;      
    padding-left: 3px; 
    opacity: 1 !important;
}

.user-full-name{
    margin-bottom: 0;
    font-weight: bold;
    word-break: break-all;
    transition: 0.3s;
    font-size: 15px;
    font-family: Montserrat;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.selected .user-full-name{
    color: #4a6f98;
    font-size: 17px;
}

.no-preview{
    font-size: 14px;
    margin-bottom: 0;
    opacity: 0.7;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.no-preview span{
    font-weight: bold;
    font-size: 12px;
    font-family: Montserrat;
    letter-spacing: 0.6px;
    opacity: 0.8;
    color: blueviolet;
}

.preview{
    margin-bottom: 0;
    opacity: 0.7;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    transition: 0.3s;
    font-size: 13px;
    margin-top: 2px;
    font-family: Montserrat;
}

.selected .preview{
    color: black;
    font-size: 14px;
}

.preview span{
    font-weight: bold;
    font-size: 12px;
}
.customScroll{
    overflow-y: scroll
}

.customScroll::-webkit-scrollbar {
    width: 5px;
}
 
.customScroll::-webkit-scrollbar-track {
    opacity: 0;
}
 
.customScroll::-webkit-scrollbar-thumb {
    background-color: rgba(128, 0, 128, 0.7);
    outline: 1px solid slategrey;    
    border-radius: 50px;
}
.primary{
    border-color: #039be530;
    border-width: 3px;
    border-style: solid;
    border-radius: 5px;
    padding: 10px;
}

.name{
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin: 0;
    font-family: Montserrat;
}

.username{
    margin: 0;
    font-size: 13px;
    font-family: Montserrat;
}

.username span{
    font-weight: bold;
    margin-right: 5px;
}

.logout{
    display: inline-block;
    border-radius: 3px;
    padding: 2px 8px;
    margin-top: 5px;
    margin-bottom: 0;
    font-size: 12px;
    background: rgb(241, 102, 52);
    color: white;
    cursor: pointer;
    font-family: Montserrat;
    font-weight: bold;
    letter-spacing: 0.5px;
    transition: 0.3s;   
    box-shadow: #bdbdbd 1px 1px 5px;
    font-weight: bold;     
}

.logout:hover{
    color: white;
    background: rgb(226, 84, 32);
    box-shadow: #bdbdbd 1px 1px 1px;
}
.tabButton{    
    margin-right: 5px;
    border-color: transparent !important;
    border-radius: 0;
    padding: 5px;
    color: #027cb9;
    outline: none !important;
    background: transparent;
    border: none;
}

.tabButton:hover{
    background: transparent !important;
    color: #01537c;
}

.tabButton.tabSelected{
    border-bottom-color: #01537c !important;
    border-bottom-style: solid;
    border-bottom-width: 2px;
}  

.tabs{
    margin-top: 5px;
}

#panel-window{
    background: transparent;
    height: 100%;
    max-height: 100%;
    overflow-y: hidden;
    padding: 0;
}

#panel-window .header{
    height: 26%;
    background: transparent;
    padding-bottom: 5px;
    padding-right: 9px;
}

.panelWindowContainer{
    padding: 0px 10px;
    height: 100%;
}

.listContainer{
    overflow-y: hidden;
    height: 74%;
    margin-top: 0px;
}

.listContainerPrimary{
    height: 100%;
}

.shown{
    display: block;
}

.hidden{
    display: none;
}

.chatWindowContainer{
    padding: 0;
    height: 100%;
    position: relative;
}

.progressContainer{
    text-align: center;
    margin-top: 45vh;
}

.primaryContainer{
    padding: 50px 10px;
    height: 100vh;
}

.primaryRow{
    height: 100%;
}

