.customScroll{
    overflow-y: scroll
}

.customScroll::-webkit-scrollbar {
    width: 4px;
}
 
.customScroll::-webkit-scrollbar-track {
    opacity: 0;
}
 
.customScroll::-webkit-scrollbar-thumb {
    background-color: rgba(95, 40, 95, 0.61);
    outline: 1px solid slategrey;    
    border-radius: 50px;
}
textarea{
    transition: 0.5s;
    overflow: hidden;
    resize: none;
    position: absolute;
    left: 0;
    top: 0;
    width: 90%;
    outline: none;
    border: none;
    margin-left: 3%;
    height: 90%;
    line-height: 14px;
    margin-top: 3px;
    padding-top: 14px;
    font-family: Oxygen;    
}

textarea:disabled{
    background: white !important;
    opacity: 0.5;
}

.sendButton{
    position: absolute !important;
    top: 0;
    right: 2px;
    height: 100%;
    border: none !important;
    outline: none !important;
}

.progress{
    height: 1rem !important;
    width: 1rem !important;
    position: absolute;
    top: 35%;
    right: 10px;
    background: transparent;
}
.msg{
    width: 100%;
    display: flex;
}

.notMyMsg{
    justify-content: flex-start;    
}

.myMsg{
    justify-content: flex-end;    
}

.notMyMsg div{
    background: #82b2ca66;    
    max-width: 50%;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.myMsg div{
    background: #f5f5f5;    
    max-width: 50%;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.more{
    color: #0174ad;
    font-weight: bold;
    font-size: 12px;
    margin-left: 10px;
    cursor: pointer;
}

.dateContainer{
    width: 100%;
    text-align: center;
    position: -webkit-sticky;
    position: sticky;
    top: 0;    
}

.dateText{
    width: 100px;
    border-radius: 5px;
    background: #7b8c94;
    color: white;
    font-size: 13px;
    display: inline-block;
    padding: 2px 10px;
    font-family: Quicksand;
    font-weight: bold;
}

.msgPrimary{
    margin: 0px 10px;
    padding: 5px 10px;
    border-radius: 5px;
    margin-top: 15px
}

.msgPrimary.noMargin{
    margin-top: 3px;
}

.msgContent{
    white-space: pre-wrap;
    margin-bottom: 2px;
    font-size: 14px;
    word-break: break-word;
    font-family: Quicksand;
    font-size: 13px;
}

.timeText{
    text-align: end;
    margin-bottom: 0px;
    font-size: 10px;
}

#list{
    overflow-y: scroll
}

.custom-scroll::-webkit-scrollbar {
    width: 5px;
}
 
.custom-scroll::-webkit-scrollbar-track {
    opacity: 0;
}
 
.custom-scroll::-webkit-scrollbar-thumb {
  background-color: rgba(128, 0, 128, 0.7);
  outline: 1px solid slategrey;    
  border-radius: 50px;
}

.placeholder{
    margin-top: 25%;
    text-align: center;
}

.placeholder h6{
    margin-top: 15px;
    font-family: Montserrat;
    opacity: 0.6;
}

.placeholder p.first{
    margin-top: 30px;
}

.placeholder p{
    font-size: 12px;
    font-family: Montserrat;
    opacity: 0.6;
    margin-bottom: 0;
}

.placeholder p span{
    font-weight: bold;
}
.no-msg-container{
    text-align: center;
    margin-top: 25vh;
}

.no-msg-container p{
    opacity: 0.6;
    font-family: Montserrat;
}


